import React, { useEffect, useState } from "react";
import { MdRadioButtonChecked, MdRadioButtonUnchecked } from "react-icons/md";
import {
  QuestionInterface,
  QuestionLanguage,
  QuestionLanguages,
  questionTypeElementsType,
} from "../../interfaces/index";
import { newAnswer } from "../../containers/DoExam/DoExam";
import { connect } from "react-redux";
import {
  Auth,
  candidateExamStoreInterface,
  examDataInterface,
  System,
  setSelectedExamQuestion,
  setQuestionLanguage,
  questionTypesStoreInterface,
  loadQuestionTypes,
  QuestionAnswersStore,
} from "../../actions";
import { StoreState } from "../../reducers";
import Loading from "../../components/Loading/Loading";
import { LoadingType } from "../../components/Loading/LoadingV2";
import {
  toCalculatedMultipleChoice,
  toMatching,
  toMultipleChoice,
  toNumerical,
  toShortAnswer,
  toSimpleCalculated,
  toTrueFalse,
} from "../../containers/DoExam/QuestionDetails/ConvertingAnswers";
import { BiWorld } from "react-icons/bi";
import { AiFillCaretDown } from "react-icons/ai";
import { FaTimes } from "react-icons/fa";
import { findQuestionType } from "../../containers/DoExam/utils/utils";
import MultipleChoice from "../../containers/DoExam/QuestionDetails/Answers/MultipleChoice";
import TrueFalse from "../../containers/DoExam/QuestionDetails/Answers/TrueFalse";
import SimpleCalculated from "../../containers/DoExam/QuestionDetails/Answers/SimpleCalculated";
import Matching from "../../containers/DoExam/QuestionDetails/Answers/Matching";
import ShortAnswer from "../../containers/DoExam/QuestionDetails/Answers/ShortAnswer";
import Numerical from "../../containers/DoExam/QuestionDetails/Answers/Numerical";
import CalculatedMultipleChoice from "../../containers/DoExam/QuestionDetails/Answers/CalculatedMultipleChoice";
import { Shuffle } from "../../utils/functions";

interface QuestionDetailsProps {
  selectedQuestion: QuestionInterface;
  setSelectedQuestion: (question: QuestionInterface) => void;
  answerQuestionFn: (answer: QuestionAnswersStore) => void;
  questionAnswers: newAnswer[] | undefined;
  doExam: examDataInterface;
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  system: System;
  setSelectedExamQuestion: (
    exam_id: string,
    question_id: string | null
  ) => void;
  setQuestionLanguage: (lang: QuestionLanguage) => void;
  questionTypes: questionTypesStoreInterface;
  loadQuestionTypes: (callback: (loading: boolean) => void) => void;
  preview: boolean;
  submitQuestionAnswer: boolean;
}

const _QuestionPreview = (props: QuestionDetailsProps) => {
  const [loading, setLoading] = useState<boolean>(false);
  const [selectLang, setSelectLang] = useState<boolean>(false);
  const [selectedLanguage, setSelectedLanguage] = useState(
    props.selectedQuestion.question[0].language
  );

  const filterLanguages = () => {
    let langs: {
      key: QuestionLanguage;
      value: string;
    }[] = [];
    for (let langItem of props.selectedQuestion.question) {
      let testLangQ = QuestionLanguages.find(
        (l) => l.key === langItem.language
      );
      if (testLangQ !== undefined) {
        langs = [...langs, testLangQ];
      }
    }
    return langs;
  };
  useEffect(() => {
    if (
      props.questionTypes.questionTypes === null ||
      props.questionTypes.questionTypes.length === 0
    ) {
      props.loadQuestionTypes(setLoading);
    }
  }, [props]);
  return (
    <div className="bg-gray-800 h-full">
      {loading === true ? (
        <div className="bg-gray-200 px-3 py-2 rounded my-2 overflow-y-auto pb-80 md:pb-60">
          <div className="rounded bg-white px-4 py-4 mt-5 mx-4">
            <div className="text-2xl mx-4 mb-3">Loading...</div>
            <Loading type={LoadingType.white} />
          </div>
        </div>
      ) : (
        <div className="bg-gray-200 px-3 py-2 my-2 overflow-y-auto pb-80 md:pb-60">
          {/* <div
            className={`h-8 ${
              props.submitQuestionAnswer === true
                ? "animate__animated animate__fadeIn bg-yellow-50 text-yellow-700 animate-pulse"
                : "text-blue-400 italic"
            } px-3 py-2 rounded  font-bold text-sm`}
          >
            {props.submitQuestionAnswer === true
              ? "Saving your answer ..."
              : ""}
          </div> */}
          <div className="flex flex-row gap-2 mt-2">
            <div className="flex flex-row justify-between w-full">
              {props.selectedQuestion.question.find(
                (itm) => itm.language === selectedLanguage
              ) && (
                <div className="text-md flex flex-col w-full">
                  <div
                    dangerouslySetInnerHTML={{
                      __html: props.selectedQuestion.question.find(
                        (itm) => itm.language === selectedLanguage
                      )!.title,
                    }}
                    className="text-sm text-gray-600"
                  ></div>
                  <div
                    dangerouslySetInnerHTML={{
                      __html: props.selectedQuestion.question.find(
                        (itm) => itm.language === selectedLanguage
                      )!.description,
                    }}
                    className="text-lg text-gray-800 font-bold"
                  ></div>
                </div>
              )}
              <div className="bg-white rounded h-10 hover:bg-white cursor-pointer">
                <div
                  onClick={() => setSelectLang(!selectLang)}
                  className="flex flex-row items-center gap-2"
                >
                  <div className="rounded-l">
                    <BiWorld className="text-2xl my-2 ml-2" />
                  </div>
                  <div className="flex flex-col" style={{ width: "97px" }}>
                    <span className="text-xs font-bold italic text-gray-700">
                      Language
                    </span>
                    <span
                      title={
                        filterLanguages().find(
                          (itm) => itm.key === selectedLanguage
                        )?.value
                      }
                      className="text-sm font-bold text-blue-600 truncate"
                      style={{ width: "97px" }}
                    >
                      {
                        filterLanguages().find(
                          (itm) => itm.key === selectedLanguage
                        )?.value
                      }
                    </span>
                  </div>
                  <div
                    className={`${
                      selectLang === true
                        ? "bg-yellow-800 text-white"
                        : "bg-gray-50 hover:bg-blue-600"
                    } h-10 rounded-r border-l border-gray-300 flex flex-row items-center justify-center cursor-pointer hover:text-white hover:border-white`}
                  >
                    {selectLang === true ? (
                      <FaTimes className="text-xl my-2 mx-2" />
                    ) : (
                      <AiFillCaretDown className="text-xl mb-1 mt-2 mx-2" />
                    )}
                  </div>
                </div>
                {selectLang === true && (
                  <div
                    className="relative rounded overflow-hidden"
                    style={{ width: "182px" }}
                  >
                    <div className="bg-blue-700 animate__animated animate__fadeIn animate__faster rounded-b w-full right-0 flex flex-col">
                      {filterLanguages().map((item, i) => (
                        <span
                          onClick={() => {
                            props.setQuestionLanguage(item.key);
                            setSelectLang(!selectLang);
                            setSelectedLanguage(item.key);
                          }}
                          key={i + 1}
                          className={`flex flex-row items-center animate__animated animate__fadeInDown animate__faster text-white text-sm px-2 py-2 border-2 ${
                            selectedLanguage === item.key
                              ? "bg-blue-800 font-extrabold border-blue-800"
                              : "bg-blue-500 text-blue-100 border-blue-500 hover:bg-blue-700 hover:border-blue-700"
                          }`}
                        >
                          {selectedLanguage === item.key ? (
                            <MdRadioButtonChecked className="text-xl mr-2" />
                          ) : (
                            <MdRadioButtonUnchecked className="text-xl mr-2" />
                          )}{" "}
                          {item.value}
                        </span>
                      ))}
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
          <div
            className="my-4 py-2 px-2 bg-white rounded"
            onClick={() => selectLang === true && setSelectLang(false)}
          >
            {/* Single and Multiple choice */}
            {props.selectedQuestion.question_id !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) !== null &&
              (findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.MultipleChoice ||
                findQuestionType(
                  props.selectedQuestion.type,
                  props.questionTypes.questionTypes
                ) === questionTypeElementsType.SingleMultiple) && (
                <MultipleChoice
                  type={findQuestionType(
                    props.selectedQuestion.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.selectedQuestion.question_id,
                    answers: toMultipleChoice(
                      props.selectedQuestion.question.find(
                        (itm) => itm.language === selectedLanguage
                      )!.answer
                    ),
                  }}
                  answerQuestionFn={props.answerQuestionFn}
                />
              )}
            {/* True and False */}
            {props.selectedQuestion.question_id !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.TrueFalse && (
                <TrueFalse
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id === props.selectedQuestion.question_id
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.selectedQuestion.question_id,
                    answers: toTrueFalse(
                      props.selectedQuestion.question.find(
                        (itm) => itm.language === selectedLanguage
                      )!.answer
                    ),
                  }}
                  answerQuestionFn={props.answerQuestionFn}
                />
              )}
            {/* Matching Question */}
            {/* {console.log(
              "Type: ",
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              )
            )} */}
            {props.selectedQuestion.question_id !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.Matching && (
                <Matching
                  selectedLanguage={selectedLanguage}
                  selectedExam={props.candidateExam.candidate_exam}
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id === props.selectedQuestion.question_id
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.selectedQuestion.question_id,
                    answers: toMatching(
                      props.selectedQuestion.question.find(
                        (itm) => itm.language === selectedLanguage
                      )!.answer
                    ),
                    shuffledAnswers: Shuffle(
                      toMatching(
                        props.selectedQuestion.question.find(
                          (itm) => itm.language === selectedLanguage
                        )!.answer
                      )
                    ),
                  }}
                  answerQuestionFn={props.answerQuestionFn}
                />
              )}
            {/* Simple Calculated */}
            {props.selectedQuestion.question_id !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.SimpleCalculate && (
                <SimpleCalculated
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id === props.selectedQuestion.question_id
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.selectedQuestion.question_id,
                    answers: toSimpleCalculated(
                      props.selectedQuestion.question.find(
                        (itm) => itm.language === selectedLanguage
                      )!.answer
                    ),
                  }}
                  answerQuestionFn={props.answerQuestionFn}
                />
              )}
            {/* Short Answer */}
            {props.selectedQuestion.question_id !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.ShortAnswer && (
                <ShortAnswer
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id === props.selectedQuestion.question_id
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.selectedQuestion.question_id,
                    answers: toShortAnswer(
                      props.selectedQuestion.question.find(
                        (itm) => itm.language === selectedLanguage
                      )!.answer
                    ),
                  }}
                  answerQuestionFn={props.answerQuestionFn}
                />
              )}
            {/* Short Answer */}
            {props.selectedQuestion.question_id !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.Numeric && (
                <Numerical
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id === props.selectedQuestion.question_id
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.selectedQuestion.question_id,
                    answers: toNumerical(
                      props.selectedQuestion.question.find(
                        (itm) => itm.language === selectedLanguage
                      )!.answer
                    ),
                  }}
                  answerQuestionFn={props.answerQuestionFn}
                />
              )}
            {/* Calculated Multiple choice */}
            {props.selectedQuestion.question_id !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) !== null &&
              findQuestionType(
                props.selectedQuestion.type,
                props.questionTypes.questionTypes
              ) === questionTypeElementsType.CalculatedMultipleChoice && (
                <CalculatedMultipleChoice
                  type={findQuestionType(
                    props.candidateExam.candidate_exam?.questions.find(
                      (itm) =>
                        itm.question_id === props.selectedQuestion.question_id
                    )?.type,
                    props.questionTypes.questionTypes
                  )}
                  available_answers={props.candidateExam.question_answers}
                  selectedQuestionAnswer={{
                    question_id: props.selectedQuestion.question_id,
                    answers: toCalculatedMultipleChoice(
                      props.selectedQuestion.question.find(
                        (itm) => itm.language === selectedLanguage
                      )!.answer
                    ),
                  }}
                  answerQuestionFn={props.answerQuestionFn}
                />
              )}
          </div>
        </div>
      )}
    </div>
  );
};

const mapStateToProps = ({
  auth,
  doExam,
  candidateExam,
  system,
  questionTypes,
}: StoreState): {
  auth: Auth;
  doExam: examDataInterface;
  candidateExam: candidateExamStoreInterface;
  system: System;
  questionTypes: questionTypesStoreInterface;
} => {
  return {
    auth: auth,
    doExam: doExam,
    candidateExam: candidateExam,
    system: system,
    questionTypes: questionTypes,
  };
};

const QuestionPreview = connect(mapStateToProps, {
  setSelectedExamQuestion,
  setQuestionLanguage,
  loadQuestionTypes,
})(_QuestionPreview);

export default QuestionPreview;
